import React, { Component } from "react";
import "./CSS/projectCards.css";

class Art extends Component {
    render() {
        return (
            <div className="cards">
                <div className="item">
                    <p className="title">my Paintings</p>
                    <p className="desc">
                        my paintings are not perfect, but i'm proud of them.
                        here you can see some of my paintings. soon I'll add my
                        other works as well
                    </p>
                    <p className="desc">
                        using pointillism method, containing thousands of points
                    </p>
                    <img src={require("../Images/painting1.jpg")} />
                    <img src={require("../Images/painting2.jpg")} />
                    <p className="desc">drawing with straight lines</p>
                    <img src={require("../Images/painting3.jpg")} />
                    <p className="desc">modern warfare</p>
                    <img src={require("../Images/painting4.jpg")} />
                    <p className="desc">
                        this one represents a religous content. even tough i'm
                        not religous, it's part of our culture and a lot of
                        people like this kind of paintings. this one was
                        displayed in my highschool main wall for more than a
                        year
                    </p>
                    <img src={require("../Images/painting5.jpg")} />
                </div>
            </div>
        );
    }
}

export default Art;
